.popup__input {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #000;
  border: none;
  width: 358px;
  height: 25px;
  padding: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
}

@media screen and (min-width: 320px) and (max-width: 580px) {
  .popup__input {
    width: 238px;
  }
}